
.ConfirmModal{
    width: 300px !important;
    max-height: 300px !important;
    top: calc(50% - 250px);
    left: calc(50% - 250px);
}

.ConfirmActionMessage{
    width: 100%;
    float: left;
    clear: left;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    text-align: center;
    margin-top: 10px;
    margin-bottom: 10px;
    color: var(--rf-fontColor, #466289);
}

.ConfirmActionAcctions{
    margin-top: 10px;
    width: 100%;
    float: left;
    clear: left;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    border-top: 1px solid var(--rf-fontColor, #466289);
}


.ConfirmActionAcctionsFirst{
    margin-left: 38%;
}

.ConfirmButton{
    width: 30px;
    height: 30px;
    color: var(--rf-buttonColor, #466289);
    background-color: transparent;
    border: none;
    float: left;
    cursor: pointer;
    outline: none;
}